#container form {
  padding: 37.5px;
  margin: 50px 0;
}

#container h1 {
  font-size: 32px;
  font-weight: 700;
  letter-spacing: 7px;
  text-align: center;
  text-transform: uppercase;
}

#container .underline {
  border-bottom: solid 2px white;
  margin: -0.512em auto;
  width: 80px;
}

#container .icon_wrapper {
  margin: 50px auto 0;
  width: 100%;
}

#container .icon {
  display: block;
  fill: white;
  height: 50px;
  margin: 0 auto;
  width: 50px;
}

#container .email {
  float: right;
  width: 45%;
}

#container input[type="text"],
#container [type="email"],
#container select,
#container textarea {
  background: none;
  border: none;
  border-bottom: solid 2px white;
  color: white;
  font-size: 1em;
  font-weight: 400;
  letter-spacing: 1px;
  margin: 0em 0 1.875em 0;
  padding: 0 0 0.875em 0;
  text-transform: uppercase;
  width: 100%;
  box-sizing: border-box;
  transition: all 0.3s;
}

#container input[type="text"]:focus,
#container [type="email"]:focus,
textarea:focus {
  outline: none;
  padding: 0 0 0.875em 0;
}

#container .message {
  float: none;
}

#container .name {
  float: left;
  width: 45%;
}

#container select {
  background: url("https://cdn4.iconfinder.com/data/icons/ionicons/512/icon-ios7-arrow-down-32.png")
    no-repeat right;
  outline: none;
  -moz-appearance: none;
  -webkit-appearance: none;
}

#container .telephone {
  width: 100%;
}

#container textarea {
  line-height: 150%;
  height: 150px;
  resize: none;
  width: 100%;
}

#container #form_button {
  background: none;
  border: solid 2px white;
  color: white;
  cursor: pointer;
  display: inline-block;
  font-family: "Helvetica", Arial, sans-serif;
  font-size: 0.875em;
  font-weight: bold;
  outline: none;
  padding: 20px 35px;
  text-transform: uppercase;
  transition: all 0.3s;
}

#container #form_button:hover {
  background: white;
  color: #272727;
}

@media screen and (max-width: 768px) {
  #container {
    margin: 20px auto;
    width: 95%;
  }
}

@media screen and (max-width: 480px) {
  #container h1 {
    font-size: 26px;
  }

  #container .underline {
    width: 68px;
  }

  #container #form_button {
    padding: 15px 25px;
  }
}

@media screen and (max-width: 420px) {
  #container h1 {
    font-size: 18px;
  }

  #container .icon {
    height: 35px;
    width: 35px;
  }

  #container .underline {
    width: 53px;
  }

  #container input[type="text"],
  [type="email"],
  #container select,
  #container textarea {
    font-size: 0.875em;
  }
}

.footer {
  display: flex;
  flex-flow: row wrap;
  padding: 30px 30px 20px 30px;
  color: white;
  background-color: rgb(14 19 31);
}

.footer > * {
  flex: 1 1 100%;
}

.footer__addr {
  margin-right: 1.25em;
  margin-bottom: 2em;
}

.footer__logo {
  font-family: "Pacifico", cursive;
  font-weight: 400;
  text-transform: lowercase;
  font-size: 1.5rem;
}

.footer__addr h2 {
  margin-top: 1.3em;
  font-size: 15px;
  font-weight: 400;
}

.nav__title {
  font-weight: 400;
  font-size: 15px;
}

.footer address {
  font-style: normal;
  color: #cfdddd;
}

.footer__btn {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 36px;
  max-width: -webkit-max-content;
  max-width: max-content;
  background-color: rgba(14, 14, 15, 0.719);
  border-radius: 100px;
  color: #e9e5e5;
  line-height: 0;
  margin: 0.6em 0;
  font-size: 1rem;
  padding: 0 1.3em;
}

.footer ul {
  list-style: none;
  padding-left: 0;
}

.footer li {
  line-height: 2em;
}

.footer a {
  text-decoration: none;
}

.footer__nav {
  display: flex;
  flex-flow: row wrap;
}

.footer__nav > * {
  flex: 1 1 50%;
  margin-right: 1.25em;
}

.nav__ul a {
  color: #cfdddd;
}

.legal {
  display: flex;
  flex-wrap: wrap;
  color: #cfdddd;
}

.legal__links {
  display: flex;
  align-items: center;
}

.heart {
  color: #2f2f2f;
}

@media screen and (min-width: 24.375em) {
  .legal .legal__links {
    margin-left: auto;
  }
}

@media screen and (min-width: 40.375em) {
  .footer__nav > * {
    flex: 1 1;
  }

  .footer__addr {
    flex: 1 1;
  }

  .footer__nav {
    flex: 2 1;
  }
}

